body {
  font-family: "sf_ui__text";
  font-weight: 400;
}

.site-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
}

.site-header .main-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.site-header .main-menu li {
  display: inline-block;
  border-right: 1px solid #666666;
  line-height: 14px;
}

.site-header .main-menu li:last-child {
  border: none;
}

.site-header .main-menu li a {
  font: 400 13px "Raleway";
  color: #404040;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0 15px;
  letter-spacing: 0.055em;
}

.site-header .main-menu li a:hover {
  text-decoration: underline;
}

.site-header .header__tel {
  font: 400 16px "sf_ui__text";
  color: #7cb845;
  text-decoration: none;
}

.site-header .header__tel span {
  font-size: 12px;
  color: #3d9ec4;
  display: block;
  text-align: right;
}

.top-block {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding: 90px 0;
  min-height: 500px;
  color: #fff;
}

.home-top__title {
  text-align: center;
  letter-spacing: 0.05em;
  font: 100 76px "Raleway";
  margin: 0 0 30px;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 10px;
}

.home-top__title::after {
  position: absolute;
  content: '';
  width: 60px;
  height: 2px;
  background: #fff;
  bottom: 0;
  left: 50%;
  margin-left: -30px;
}

.home-top__subtitle {
  font-size: 18px;
  margin: 0;
  text-align: center;
}

.order-form {
  max-width: 830px;
  margin: 60px auto 0;
  padding: 40px;
  background: rgba(25, 76, 102, 0.9);
}

.order-form__tabs {
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}

.order-form__tabs-item {
  background: url(../img/tab-ico.png) left top 4px no-repeat;
  padding-left: 15px;
  color: #fff;
  font-size: 14px;
  text-decoration: none;
  margin-right: 20px;
  display: inline-block;
  position: relative;
}

.order-form__tabs-item::after {
  content: '';
  display: none;
  height: 5px;
  width: 5px;
  background: #7fdbff;
  border-radius: 50%;
  position: absolute;
  left: 2px;
  top: 6px;
}

.order-form__tabs-item:hover::after,
.order-form__tabs-item.active::after {
  display: block;
}

.order-form__tables {
  padding-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.25);
}

.order-form__table {
  width: 100%;
  table-layout: fixed;
}

.order-form__table th {
  text-align: left;
  font-weight: 400;
  font-size: 14px;
}

.order-form__table th,
.order-form__table td {
  padding-bottom: 10px;
}

.order-form__table td {
  padding-right: 15px;
}

.order-form__table input {
  display: block;
  max-width: 100%;
}

.order-form__passengers {
  width: 90px;
  position: relative;
  padding-right: 0 !important;
}

.order-form__date {
  width: 135px;
}

.order-form__input {
  width: 100%;
  height: 42px;
  background-color: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.1);
  outline: 0;
  padding: 0 10px 0 30px;
  font-size: 14px;
  background-position: left 10px center;
  background-repeat: no-repeat;
  color: #fff;
}

.order-form__input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.order-form__input::-moz-placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.order-form__input:-moz-placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.order-form__input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.6);
}

.order-form__city {
  background-image: url(../img/order-loc.png);
}

.order-form__date-input {
  background-image: url(../img/order-date.png);
}

.order-form__passengers-input {
  text-align: center;
  padding: 0 20px;
  color: rgba(255, 255, 255, 0.6);
  -webkit-appearance: none;
  appearance: none;
  -moz-appearance: textfield;
}

.order-form input[type=number]::-webkit-inner-spin-button,
.order-form input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.order-form .spin {
  position: absolute;
  background: url(../img/spin-less.png);
  width: 7px;
  height: 12px;
  top: 16px;
  left: 10px;
  cursor: pointer;
}

.order-form .spin.input-number-more {
  left: auto;
  right: 10px;
  background: url(../img/spin-right.png);
}